// 產品頁面
// API
const api_path = 'calon';
const baseUrl = 'https://livejs-api.hexschool.io';
const product_path = `api/livejs/v1/customer/${api_path}/products`;
const carts_path = `api/livejs/v1/customer/${api_path}/carts`;
const customOrder_path = `api/livejs/v1/customer/${api_path}/orders`;

// DOM
const categorySelector = document.querySelector('[name="categorySelector"]');
const productList = document.querySelector('ul[data-js="productList"]');
const cartList = document.querySelector('.cartList');
const sendOrderInfoBtn = document.querySelector('[data-js="sendOrderInfoBtn"');

// data
let productsData;
let cartsData;

// event
categorySelector.addEventListener('change', productFilter);
productList.addEventListener('click', addCart);
cartList.addEventListener('click', deleteCartProduct);
sendOrderInfoBtn.addEventListener('click', sendOrderInfo);
cartList.addEventListener('change', changeQuantity);

init();

function init() {
  getProductsData();
};

function getProductsData() {
  axios.get(`${baseUrl}/${product_path}`).then(function (res) {
    productsData = res.data.products;
    renderCategorySelector();
    getCartListData(); // 先跑購物車資料 後面渲染 productList 需要購物車資料
  }).catch(function (err) {
    let errData = err.response.data;
    if (errData.status === false) {
      console.log(err.response.data.message);
    };
  }).then(function () {
    const loadingAnimation = document.querySelector('.productList-loading');
    loadingAnimation.setAttribute('data-loading', false);
  });
};

function renderCategorySelector() {
  let categoryAry = [];

  productsData.forEach(function (item) {
    categoryAry.push(item.category);
  });
  categoryAry = categoryAry.filter(function (item, index, ary) {
    return ary.indexOf(item) == index;
  });

  let optionStr = '<option value="全部" selected>全部</option>';

  categoryAry.forEach(function (item) {
    optionStr += `<option value="${item}">${item}</option>`;
  });
  categorySelector.innerHTML = optionStr;
};

function renderProductList(data) {
  let listStr = '';
  data.forEach(function (item) {
    let check = cartsData.carts.some(function (product) { return product.product.id == item.id });
    let price = item.price.toLocaleString();
    let originPrice = item.origin_price.toLocaleString();
    listStr += `
      <li>
        <div class="bg-cover h-75 relative" style="background: url('${item.images}');">
          <div class="absolute top-3 -right-1 bg-dark text-white py-2 px-6">新品</div>
        </div>
        <button class="py-3 mb-2 ${check ? 'bg-primary' : 'bg-dark'} ${check ? '' : 'hover:bg-secondary'} text-white text-lg w-full
        transition duration-500 ${check ? 'cursor-default' : ''}" data-js="addCartBtn" data-id="${item.id}" ${check ? 'disabled' : ''}>
          ${check ? '已加入購物車' : '加入購物車'}
        </button>
        <div class="text-lg">
          <h5>${item.title}</h5>
          <p class="line-through">NT$${originPrice}</p>
          <p class="text-3xl">NT$${price}</p>
        </div>
      </li>
    `
  });
  productList.innerHTML = listStr;
};

function getCartListData() {
  axios.get(`${baseUrl}/${carts_path}`).then(function (res) {
    cartsData = res.data;
    productFilter();

    const cartMsg = document.querySelector('p[data-js="cartMsg"]');
    const cartTable = document.querySelector('table[data-js="cartMsg"]');
    if (cartsData.carts.length === 0) {
      cartMsg.setAttribute('data-cartMsg', true);
      cartTable.setAttribute('data-cartMsg', false);
    } else {
      cartMsg.setAttribute('data-cartMsg', false);
      cartTable.setAttribute('data-cartMsg', true);
      renderCartList();
    }
  }).catch(function (err) {
    let errData = err.response.data;
    if (errData.status === false) {
      console.log(err.response.data.message);
    }
  }).then(function () {
    const loadingAnimation = document.querySelector('.cart-loading');
    loadingAnimation.setAttribute('data-loading', false);
  });
};

function renderCartList() {
  let str = '';
  cartsData.carts.forEach(function (item) {
    let price = item.product.price.toLocaleString();
    let totalPrice = (item.product.price * item.quantity).toLocaleString();
    str += `
    <tr class="border-b border-gray-300">
    <td class="flex items-center py-5">
    <img class="w-20 h-20 object-cover mr-4" src="${item.product.images}" alt="產品圖片">
    <h5>${item.product.title}</h5>
    </td>
    <td class="py-5">NT$${price}</td>
    <td class="py-5">
    <input class="w-12 border border-gray-300 rounded px-2 text-center" data-id="${item.id}" data-js="quantityInput" type="text" value="${item.quantity}">
    </td>
    <td class="py-5">NT$${totalPrice}</td>
    <td class="text-center py-5">
    <button class="material-icons hover:text-danger transition duration-500 p-2" href="#" data-js="deleteProductBtn" data-id="${item.id}">close</button>
    </td>
    </tr>
    `;
  });
  let finalTotal = cartsData.finalTotal.toLocaleString();
  str += `
    <tr>
      <td class="py-5">
        <button class="border rounded py-2 px-5 hover:bg-dark hover:text-white transition duration-500" data-js="deleteAllProductsBtn">刪除所有品項</button>
      </td>
      <td class="py-5"></td>
      <td class="py-5"></td>
      <td class="py-5">總金額</td>
      <td class="py-5 text-3xl">NT$${finalTotal}</td>
    </tr>
    `;
  cartList.innerHTML = str;
};

function productFilter() {
  let category = categorySelector.value;
  let filteredAry = [];
  if (category === '全部') {
    renderProductList(productsData);
  } else {
    productsData.forEach(function (item) {
      if (item.category == category) {
        filteredAry.push(item);
      };
    });
    renderProductList(filteredAry);
  };
};

function addCart(e) {
  if (e.target.nodeName !== 'BUTTON') { return };
  let id = e.target.dataset.id;
  let obj = {
    "data": {
      "productId": id,
      "quantity": 1
    }
  };
  axios.post(`${baseUrl}/${carts_path}`, obj).then(function (res) {
    getCartListData();
    productFilter(); // 不呼叫 renderProductList() 避免已篩選渲染的 product list 出錯
    popUpSuccessMsg('addCartBtn');
  }).catch(function (err) {
    let errData = err.response.data;
    if (errData.status === false) {
      console.log(err.response.data.message);
    }
  });
};

function deleteCartProduct(e) {
  if (e.target.dataset.js !== 'deleteProductBtn' && e.target.dataset.js !== 'deleteAllProductsBtn') {
    return;
  } else if (e.target.dataset.js === 'deleteAllProductsBtn') { // 刪除購物車全部商品
    const popUp = document.querySelector('[data-js="doubleCheckpopUp"]');
    popUp.setAttribute('data-popUp', true);
    popUp.addEventListener('click', function (e) {
      let btnProp = e.target.getAttribute('data-dblCheckBtn');
      // double check
      if (btnProp === 'cancel' || Object.is(popUp, e.target)) { // 按取消或非訊息視窗時隱藏 double check div
        popUp.setAttribute('data-popUp', false);
      } else if (btnProp === 'delete') {
        axios.delete(`${baseUrl}/${carts_path}`).then(function (res) {
          getCartListData();
        }).catch(function (err) {
          let errData = err.response.data;
          if (errData.status === false) {
            console.log(err.response.data.message);
          };
        }).then(function () {
          popUp.setAttribute('data-popUp', false);
        });
      };
    });
  } else { // 刪除購物車單一商品
    let id = e.target.dataset.id;
    axios.delete(`${baseUrl}/${carts_path}/${id}`).then(function (res) {
      getCartListData();
    }).catch(function (err) {
      let errData = err.response.data;
      if (errData.status === false) {
        console.log(err.response.data.message);
      }
    });
  };
};

function sendOrderInfo() {
  // 錯誤訊息初始化
  let spanAry = document.querySelectorAll(`span[data-msg]`);
  spanAry.forEach(function (el) {
    el.setAttribute('class', 'invisible text-danger');
  });

  // 表單驗證
  const form = document.querySelector('[data-js="orderForm"]');
  let constraints = {
    customerName: {
      presence: {
        message: '^請輸入您的姓名'
      },
    },
    customerTel: {
      presence: {
        message: '^請輸入您的聯絡電話'
      },
      format: {
        pattern: '[0-9]+',
        flags: 'i',
        message: '^聯絡電話格式不正確'
      },
      length: {
        minimum: 9,
        message: '^聯絡電話格式不正確'
      }
    },
    customerEmail: {
      presence: {
        message: '^請輸入您的 Email'
      },
      email: {
        message: '^Email 格式不正確'
      }
    },
    customerAdd: {
      presence: {
        message: '^請輸入您的寄送地址'
      }
    }
  };

  let errors = validate(form, constraints); // 錯誤回傳 object  正確回傳 undefined
  if (errors) {
    let keys = Object.keys(errors);
    keys.forEach(function (key) {
      let el = document.querySelector(`[data-msg=${key}]`);
      el.innerHTML = `<i class="fas fa-exclamation-circle mr-1"></i>${errors[key][0]}`;
      el.classList.toggle('invisible');
    });
  } else {
    let nameValue = document.querySelector('#customerName').value;
    let telValue = document.querySelector('#customerTel').value;
    let emailValue = document.querySelector('#customerEmail').value;
    let addressValue = document.querySelector('#customerAdd').value;
    let paymentValue = document.querySelector('#paymentMethod').value;

    let obj = {
      "data": {
        "user": {
          "name": nameValue.trim(),
          "tel": telValue.trim(),
          "email": emailValue.trim(),
          "address": addressValue.trim(),
          "payment": paymentValue.trim()
        }
      }
    };

    axios.post(`${baseUrl}/${customOrder_path}`, obj).then(function (res) {
      getCartListData();
      popUpSuccessMsg('sendOrderInfoBtn');
      const orderInputs = document.querySelectorAll('[data-js="orderInput"]');
      orderInputs.forEach(function (item) {
        item.value = '';
      });
    }).catch(function (err) {
      let errData = err.response.data;
      if (errData.status === false) {
        console.log(err.response.data.message);
      }
    })
  };
};

function changeQuantity(e) {
  let value = e.target.value;
  let id = e.target.dataset.id;
  let product = cartsData.carts.filter(function (item) { return item.id == id });
  let quantityNotChanged = product[0].quantity == value; // 判斷數量有沒有改變
  // 最後一個判斷條件 不可以帶有小數點
  if (e.target.dataset.js !== 'quantityInput' || isNaN(value) || quantityNotChanged || (value - Math.round(value) !== 0)) { return };
  let obj = {
    "data": {
      "id": id,
      "quantity": Number(value)
    }
  };
  axios.patch(`${baseUrl}/${carts_path}`, obj).then(function (res) {
    getCartListData();
  }).catch(function (err) {
    let errData = err.response.data;
    if (errData.status === false) {
      console.log(err.response.data.message);
    };
  });
};

// 加入購物車、訂單資料成功送出提示視窗
function popUpSuccessMsg(btnProp) {
  const popUpMsg = document.querySelector('[data-js="popUpBlock"] p');
  if (btnProp === 'sendOrderInfoBtn') {
    if (cartsData.carts.length === 0) {
      popUpMsg.innerHTML = `<i class="fas fa-times-circle fa-4x text-danger mb-4"></i>購物車內沒有東西`;
    } else {
      popUpMsg.innerHTML = `<i class="fas fa-check-circle fa-4x text-success mb-4"></i>訂單成功送出`;
    };
  } else if (btnProp === 'addCartBtn') {
    popUpMsg.innerHTML = `<i class="fas fa-check-circle fa-4x text-success mb-4"></i>成功加入購物車`;
  };

  const popUp = document.querySelector('[data-js="popUpBlock"]');
  let styleOpacity = parseFloat(popUp.style.opacity);
  popUp.setAttribute('data-popUp', true);
  let fadeInBlock = window.setInterval(function () {
    // div 逐漸浮出
    if (styleOpacity < 10) {
      styleOpacity += 0.5;
      popUp.style.opacity = styleOpacity;
    } else {
      window.clearInterval(fadeInBlock); // 停止浮出

      // div 逐漸消失
      let fadeOutBlock = window.setInterval(function () {
        if (styleOpacity > 0) {
          styleOpacity -= 0.5;
          popUp.style.opacity = styleOpacity;
        } else {
          popUp.setAttribute('data-popUp', false);
          window.clearInterval(fadeOutBlock);
        };
      }, 50);
    };
  }, 30);
};